.basket {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin-bottom: 30px;

  &__product {
    border: 1px solid black;
    padding: 10px;
    border-radius: 4px;

    .product-id {
      margin-bottom: 8px;
    }

    .product-name {
      margin-bottom: 8px;
    }
  }

  &__count {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 8px;

    .button-add {
      background: none;
      border: 1px solid lightgray;
      border-radius: 50%;
      padding: 2.5px 2px;
      width: 20px;
      height: 20px;
      margin-right: 6px;
      cursor: pointer;
    }

    .basket-delete {
      background: none;
      border: 1px solid lightgray;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      margin-left: 6px;
      cursor: pointer;
    }
  }

  &__product-coast {
    font-weight: bold;
  }
}

.basket-total-coast {
  font-size: 150%;
  font-weight: bold;
  margin-bottom: 30px;
}

.basket-buttons {
  display: flex;
  justify-content: space-between;

  &__delete {
    padding: 14px;
    background: #282c34;
    border: 1px solid grey;
    border-radius: 6px;
    color: white;
    cursor: pointer;
  }

  &__jump-button {
    text-decoration: none;

    .button-back {
    padding: 14px;
      border-radius: 6px;
      background: white;
      border: 1px solid black;
      cursor: pointer;

    }
  }
}